.CitySelector {
  width: 500px;
  height: 400px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
  position: absolute;
  top: 40%;
  left: 50%;
  transform: translate(-50%,-50%);
  border-radius: 1em;
}
.CitySelector-search {
  width: 100%;
  padding: 10px 30px;
  box-sizing: border-box;
}
.CitySelector-inputWrap {
  width: 300px;
  margin: 0 auto;
}
.CitySelector-input {
  width: 100%;
  color: #888;
  text-align: center;
  font-size: 20px;
  border: 0;
  padding: 10px;
  border-bottom: 2px solid #f1f1f1;
  transition: 200ms
}
.CitySelector-input:focus {
  border-bottom: 2px solid rgb(148, 196, 255);
}

.CitySelector-list {
  height: 300px;
  overflow: scroll;
  margin-top: 20px;
}
.CitySelector-listItem {
  padding: 5px 10px;
  cursor: pointer;
  transition: 200ms
}

.CitySelector-listItem:hover {
  background: rgb(148, 196, 255);
  color: #fff;
  border-radius: 5px;
}